#landing {
  background: url("../img/bg.gif") no-repeat;
  height: 94vh;
  width: 100%;
  position: relative;
}
#landing > img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
#landing > .overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}
.text > a:hover {
  background: rgba(54, 102, 111, 0.9);
  font-weight: bolder;
  color: black !important;
  background: rgba(255, 255, 255, 0.6);
}



